@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "VazirVF";

  font-style: normal;

  src: url("./fonts/vazir/Vazir-Variable.woff2") format("woff2 supports variations"),
    url("./fonts/vazir/Vazir-Variable.woff2") format("woff2-variations"),
    url("./fonts/vazir/Vazir-Variable.woff") format("woff-variations");
}

@font-face {
  font-family: Vazir;

  src: url("./fonts/vazir/Vazir-Regular.eot");

  src: url("./fonts/vazir/Vazir-Regular.eot?#iefix") format("embedded-opentype"),
    url("./fonts/vazir/Vazir-Regular.woff2") format("woff2"),
    url("./fonts/vazir/Vazir-Regular.woff") format("woff"),
    url("./fonts/vazir/Vazir-Regular.ttf") format("truetype");

  font-weight: normal;

  font-style: normal;
}

@font-face {
  font-family: Vazir;

  src: url("./fonts/vazir/Vazir-Bold.eot");

  src: url("./fonts/vazir/Vazir-Bold.eot?#iefix") format("embedded-opentype"),
    url("./fonts/vazir/Vazir-Bold.woff2") format("woff2"),
    url("./fonts/vazir/Vazir-Bold.woff") format("woff"),
    url("./fonts/vazir/Vazir-Bold.ttf") format("truetype");

  font-weight: bold;

  font-style: normal;
}

@font-face {
  font-family: Vazir;

  src: url("./fonts/vazir/Vazir-Black.eot");

  src: url("./fonts/vazir/Vazir-Black.eot?#iefix") format("embedded-opentype"),
    url("./fonts/vazir/Vazir-Black.woff2") format("woff2"),
    url("./fonts/vazir/Vazir-Black.woff") format("woff"),
    url("./fonts/vazir/Vazir-Black.ttf") format("truetype");

  font-weight: 900;

  font-style: normal;
}


@font-face {
  font-family: Vazir;

  src: url("./fonts/vazir/Vazir-Light.eot");

  src: url("./fonts/vazir/Vazir-Light.eot?#iefix") format("embedded-opentype"),
    url("./fonts/vazir/Vazir-Light.woff2") format("woff2"),
    url("./fonts/vazir/Vazir-Light.woff") format("woff"),
    url("./fonts/vazir/Vazir-Light.ttf") format("truetype");

  font-weight: 300;

  font-style: normal;
}

@font-face {
  font-family: Vazir;

  src: url("./fonts/vazir/Vazir-Thin.eot");

  src: url("./fonts/vazir/Vazir-Light.eot?#iefix") format("embedded-opentype"),
    url("./fonts/vazir/Vazir-Thin.woff2") format("woff2"),
    url("./fonts/vazir/Vazir-Thin.woff") format("woff"),
    url("./fonts/vazir/Vazir-Thin.ttf") format("truetype");

  font-weight: 100;

  font-style: normal;
}

html {
  font-family: Vazir, ui-sans-serif, system-ui, -apple-system,
    BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial,
    "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol", "Noto Color Emoji";
  scroll-behavior: smooth;
}